import Cookie from "js-cookie"
export default {
    namespaced: true,
    //当用户第一次登录后，服务端生成一个token并返回给客户端，以后客户端只需要带着token请求数据即可，不需再使用用户名和密码。
    state: {
        token: ''
    },
    mutations: {
        setToken(state, val) {
            state.token = val
            Cookie.set('token', val)
        },
        clearToken(state) {
            state.token = ''
            Cookie.remove('token')
        },
        getToken(state) {
            state.token = state.token || Cookie.get('token')
        }
    }
}
