import Vue from 'vue'
import { MessageBox } from 'element-ui'
import {
  Button, Select, Radio, Container, Header,
  Aside, Main, Menu, Submenu, MenuItem,
  MenuItemGroup, RadioButton, RadioGroup,
  Icon, Avatar, Dropdown, DropdownItem, DropdownMenu,
  Row, Col, Card, Divider, Table, TableColumn, BreadcrumbItem,
  Breadcrumb, Tag, Form, FormItem, Input, DatePicker, Option,
  Dialog,
  Pagination,
  Message
} from 'element-ui';
import App from './App.vue'
import router from './router'
import store from './store'
import http from 'axios'
import '../api/mock.js'

Vue.prototype.$ELEMENT = { size: 'small', zIndex: 3000 };
Vue.use(Button)
Vue.use(Select)
Vue.use(Radio)
Vue.use(Container)
Vue.use(Header)
Vue.use(Aside)
Vue.use(Main)
Vue.use(Menu)
Vue.use(Submenu)
Vue.use(MenuItem)
Vue.use(MenuItemGroup)
Vue.use(RadioButton)
Vue.use(RadioGroup)
Vue.use(Icon)
Vue.use(Avatar)
Vue.use(Dropdown)
Vue.use(DropdownItem)
Vue.use(DropdownMenu)
Vue.use(Row)
Vue.use(Col)
Vue.use(Card)
Vue.use(Divider)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Breadcrumb)
Vue.use(BreadcrumbItem)
Vue.use(Tag)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(DatePicker)
Vue.use(Option)
Vue.use(Dialog)
Vue.use(Pagination)


Vue.use(store)

Vue.prototype.$http = http
Vue.config.productionTip = false
Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$message = Message

//导航守卫
router.beforeEach((to, from, next) => {
  store.commit('user/getToken')
  const token = store.state.user.token
  if (!token && to.name !== 'login') {
    next({ name: 'login' })
  }else if (token && to.name === 'login') {
    next({ name: 'home' })
  } else { next() }
})

new Vue({
  router,
  store,
  render: h => h(App),
  created() { this.$store.commit('tab/addMenu', router) }
}).$mount('#app')
